
.App-header {
  max-height: 60vh;
  font-size: large;
}

body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 90%;
  text-align: center;
  background: white;
}

form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

label {
  font-weight: 600;
  color: #333;
  display: flex;
  flex-direction: column;
  font-size: 14px;
}

input,
select {
  margin-top: 8px;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 14px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input:focus,
select:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
  outline: none;
}

.error {
  color: red;
  font-size: 12px;
  margin-top: 4px;
  font-weight: 500;
}

button {
  padding: 14px;
  background-color: #007bff;
  border: none;
  color: white;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:hover {
  background-color: #0056b3;
  transform: scale(1.02);
}

@media (max-width: 480px) {
  .App {
    padding: 25px 20px;
  }

  label {
    font-size: 13px;
  }

  input,
  select {
    padding: 10px;
    font-size: 13px;
  }

  button {
    padding: 12px;
    font-size: 15px;
  }
}
